<template>
	<view class="box">
		<view class="choose-area">
			<view class="primary-border" @tap="chooseRegion('ALL')">
				不限
			</view>
			<view class="primary-border" @tap="regionVisible=true">
				按区域
			</view>
			<view class="primary-border" @tap="getLocation">
				从地图选择
			</view>
		</view>
		<view class="range-area" v-if="address">
			<view class="title">
				选择：
			</view>
			<view class="right">
				<template v-for="(item,k) in distance_map">
					<view :key="k" class="primary-border" v-if="item.num=='不限'" @tap="chooseDistance(k)">
						{{address}} {{item.num}}
					</view>
					<view :key="k" class="primary-border" v-else @tap="chooseDistance(k)">
						{{address}} <text class="color-primary">≤{{item.num}} {{item.unit}}</text>
					</view>
					<view class="primary-border clear" v-if="k==distance_map.length-1" @tap="address=''">
						清空
					</view>
				</template>
			</view>
		</view>
		<map class='map' :latitude="lat1" :longitude="lon1" :markers="covers"></map>
		<w-picker :visible.sync="regionVisible" mode="region" :value="regionNumber" default-type="value" :hide-area="false"
		 @confirm="cityConfirm($event)" ref="region"></w-picker>
	</view>
</template>

<script>
	import {
		mapState
	} from 'vuex'
	let regionFlag = "ALL";
	export default {
		data() {
			return {
				lon1: "",
				lat1: "",
				covers: [{
					latitude: "",
					longitude: "",
					iconPath: '/static/images/location.png',
				}],
				address: "", //地名
				distance_index: 0, //距离
				distance_map: [{
						num: '5',
						unit: '公里',
						value: "5000"
					},
					{
						num: '10',
						unit: '公里',
						value: "10000"
					},
					{
						num: '15',
						unit: '公里',
						value: "15000"
					}
				],
				regionNumber: ["110000", "110100", "110101"], //默认选中的地址
				regionVisible: false
			}
		},
		computed: {
			...mapState({
				location: state => state.location
			})
		},
		onLoad(option) {
			this.lon1 = this.location.lon1;
			this.lat1 = this.location.lat1;
			this.covers[0].latitude = this.location.lat1;
			this.covers[0].longitude = this.location.lon1;
		},
		methods: {
			// 点击不限
			chooseRegion(k) {
				let range_obj = {
					lon1: this.lon1,
					lat1: this.lat1,
					regionFlag: k,
					distance: "",
					province:"",
					city:"",
					region:""
				};
				this.$store.commit("saveIssueMap", range_obj);
				uni.navigateBack()
			},
			// 区域选择
			cityConfirm(event) {
				let range_obj = {
					lon1: this.lon1,
					lat1: this.lat1,
					regionFlag: 'REGION',
					distance: "",
					province:event.obj.province.label,
					city:event.obj.city.label,
					region:event.obj.area.label
				};
				this.$store.commit("saveIssueMap", range_obj);
				uni.navigateBack()
			},
			// 选择地图距离
			chooseDistance(k) {
				let range_obj = {
					lon1: this.lon1,
					lat1: this.lat1,
					regionFlag: 'RANGE',
					distance: this.distance_map[k].value,
					distance_zh:this.distance_map[k].num+this.distance_map[k].unit,
					address:this.address,
					province:"",
					city:"",
					region:""
				};
				this.$store.commit("saveIssueMap", range_obj);
				uni.navigateBack()
			},
			getLocation(){
				uni.chooseLocation({
					latitude:this.lat1,
					longitude:this.lon1,
					success: (res) => {
						console.log(res);
						this.address = res.name
						this.lon1 = res.longitude
						this.lat1 = res.latitude;
						this.covers[0].latitude = res.latitude;
						this.covers[0].longitude = res.longitude
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	.color-primary {
		color: $color-primary;
	}

	.range-area {
		width: calc(100% - 68rpx);
		margin: 0 auto;
		display: flex;
		font-size: 24rpx;
		justify-content: space-between;
		align-items: center;

		.title {
			width: 80rpx;
			flex-shrink: 0;
		}

		.right {
			display: flex;
			font-size: 24rpx;
			flex-wrap: wrap;

			.primary-border {
				margin-bottom: 16rpx;
				margin-right: 16rpx;
			}

			.clear {
				border: none;
				color: #333;
			}
		}
	}

	.box {
		.choose-area {
			display: flex;
			justify-content: space-between;
			margin: 30rpx auto;
			width: 600rpx;


		}

		.map {
			width: 100%;
			height: 100vh;
		}
	}

	.primary-border {
		color: #8b848a;
		border-color: #8b848a;
		padding: 16rpx 40rpx;
		text-align: center;
	}
</style>
